body {
    background: #004980;
}

.triesid {
    color: #004980;
}

.hidden {
    display: none;
}

.container {
    padding-top: 52px;
    text-align: center;
    width: 480px;
    height: 459px;
    margin-left: 33%;
    margin-top: 100px;
    background: #c2f5ff;
    border-radius: 12px;
}

.title {
    margin-bottom: 48px;
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 48px;
    color: #004980;
}

.button {
    width: 98px;
    height: 98px;
    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 72px;
    color: #0080c1;
    background: #fff;
    border-radius: 12px;
}

.start {
    margin-top: 67px;
    width: 246px;
    height: 50px;
    background: #7ac100;
    border-radius: 12px;
    border: 2px solid #fff;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: #fff;
}

.timer__button {
    display: flex;
    justify-content: space-between;
}

.timer {
    margin-left: 30px;
    margin-top: 41px;
    display: flex;
}

.time {
    font-size: 64px;
    margin: 0;
    color: #fff;
}

.restart {
    margin-right: 23px;
    margin-top: 52px;
    width: 246px;
    height: 48px;
    font-weight: 400;
    font-size: 24px;
    text-align: center;
    color: #fff;
    background: #7ac100;
    border: 1px solid #7ac100;
    border-radius: 12px;
}

.game {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    justify-content: center;
}

.cart {
    width: 95px;
    height: 133px;
    background: #fff;
    border-radius: 4px;
}

.container__cards {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    text-align: center;
    margin-left: 30px;
}

[data-view="card"] {
    transform: rotateY(180deg);
    width: 95px;
    height: 135px;
    margin: 1px;
    float: left;
    cursor: pointer;
    background: url("./img/рубашка.png") left center no-repeat;
    border-radius: 5px;
}

.flipped {
    transition: 0.2s;
    transform-style: preserve-3d;
    position: relative;
    transform: rotateY(0deg);
}

.reverse {
    transition: 0.2s;
    transform-style: preserve-3d;
    position: relative;
    transform: rotateY(180deg);
}

.correct {
    transition: 0.8s;
    opacity: 0.2;
    cursor: default;
    transform-style: preserve-3d;
    position: relative;
    transform: translateX(-5000%);
}

